<template>
  <div>
    <h1>{{ id ? '编辑' : '新建' }}管理员</h1>
    <el-form label-width="120px" @submit.native.prevent="save">
        <el-form-item  label="用户名">
          <el-input v-model="model.username" />
        </el-form-item>
        <el-form-item  label="密码">
          <el-input  type="password" v-model="model.password" />
        </el-form-item>

        <el-form-item>
          <el-button type="primary" native-type="submit">{{  id ? '编辑' : '保存'  }}</el-button>
        </el-form-item>

      </el-form>
  </div>
</template>

<script>
  import { VueEditor } from 'vue2-editor'
  export default  {
  	data() {
  		return {
			  model: {
				  categories: ''
        },
			  categories: [],
        parents: [],
        parent: '',
      }
    },
    methods: {

	    async save() {
        let res = null
        if (this.id) {
	        res = await this.$http.put('rest/admin_users/'+ this.id, this.model)
        } else {
	        res = await this.$http.post('rest/admin_users', this.model)
        }


        this.$router.push('/admin_users/list')
        this.$message({
          type: 'success',
          message: '保存成功了'
        })
      },
      async fetch() {
	      let { data } = await this.$http.get(`rest/admin_users/${ this.id }`)
        console.log(data)
        this.model = data
      }
    },
    created() {
	    this.id && this.fetch()
    },
    props: {
	    id: {}
    },
    components: {
	    VueEditor
    }
  }
</script>